import {Col, Row} from "react-bootstrap";
import React, {useEffect, useState} from 'react';
import {SezioneDocumento} from "./common/SezioneDocumento";
import {K} from "../Constants";
import {apiDettaglioDocumenti, apiDettaglioPratica, apiDownloadDocumentazione} from "../http/api";
import {useQuery} from "../http/helpers";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload} from "@fortawesome/free-solid-svg-icons";


export const Documentazione = ({isProspect, valid, nascondiDocs, changeDocsCaricati}) => {

  let query = useQuery().get('guid');
  const [response, setResponse] = useState([]);
  const [r, refresh] = useState([0, 1])
  const [statoPratica, setStatoPratica] = useState();
  function allAreApprovati(array) {
    console.log(array);
    return array.filter(x=>x.tipoDocumento !== "Altro").every(element => {
      return element.statoDocumento === "APPROVATO";
    });
  }


  function allAreCaricati(array){
    return array.filter(x=>x.obbligatorio).every(element => {
      return element.listaDocumenti.length > 0;
    });
  }

  const getDocumentazione = () => {
    let query = useQuery().get('guid');
    apiDettaglioDocumenti(K.serviceUrl, query)
      .then(json => {
        setResponse([...json]);
        changeDocsCaricati(allAreCaricati(json));
        valid(allAreApprovati(json));
      })
      .catch(e => console.log(e));
  };

  const getStatoPratica = () => {
    apiDettaglioPratica(K.serviceUrl, query).then(json => {
      setStatoPratica(json.statoPratica)
    })
  }



  const downloadAll = () => {
    let query = useQuery().get('guid');
    return apiDownloadDocumentazione(K.serviceUrl, query);
  };


  useEffect(() => {
    getDocumentazione();
    getStatoPratica();
  }, []);

  const docs = (
    <>
      <p>TEST</p>
    </>
  )

  return (
    <>
      {!isProspect && nascondiDocs &&
        <div className={'white-rounded-shadowed mt-4'}>
          <Col md={1}>

            <p className={'desc-text'}>DOCUMENTAZIONE</p>
          </Col>
          <p className={'title text-center'}>In attesa della documentazione del cliente</p>
        </div>}

      { (!nascondiDocs && !isProspect  || isProspect) &&
        <div className={'white-rounded-shadowed mt-4'}>
        <Row className={'align-to-end'}>
          <Col md={1}>
            <p className={'desc-text'}>DOCUMENTAZIONE</p>
          </Col>
          <Col md={11}>
            <p className={'desc-text'}>*Campi Obbligatori</p>
          </Col>
        </Row>
        {!isProspect &&
          <Row className={'align-to-end'}>
            <Col md={13}>
        <span className={'primary-color btn'} onClick={() => downloadAll()}>Scarica tutto
            <FontAwesomeIcon icon={faDownload} className={' ms-1 primary-color'}></FontAwesomeIcon></span>
            </Col>
          </Row>}

        <Row className={'align-to-start mt-4'}>
          <Col md={5}>
            <p className={'desc-text margin-custom-l'}>DOCUMENTI {isProspect?"DA CARICARE":""}</p>
          </Col>
          <Col md={3}>
            <p className={'desc-text'}>FILE</p>
          </Col>
          <Col md={2}>
            <p className={'desc-text'}>STATO</p>
          </Col>
          <Col md={2}></Col>
        </Row>
        {response
          .filter(value => {
            return value.tipoDocumento !== "Altro"
          })
          .map((value, index) => <SezioneDocumento key={index} tipoDocumento={value.tipoDocumento}
                                                   classeDocumentoId={value.idClasseDocumento}
                                                   statoDocumento={value.statoDocumento}
                                                   statoPratica={statoPratica}
                                                   listaDoc={value.listaDocumenti}
                                                   notaFromDoc={value.nota}
                                                   isProspect={isProspect}
                                                   update={getDocumentazione} required={value.obbligatorio}/>)
        }


      </div>}
      { (!nascondiDocs && !isProspect  || isProspect) &&
      <div className={'white-rounded-shadowed mt-4'}>
        <Row className={'align-to-start'}>
          <Col md={12}>
            <p className={'desc-text'}>DOCUMENTAZIONE AGGIUNTIVA</p>
          </Col>
        </Row>

        <Row className={'align-to-start mt-4'}>
          <Col md={10}>
            <p className={'desc-text margin-custom-l'}>CARICA DOCUMENTI</p>
          </Col>

          <Col md={2}></Col>
        </Row>
        {response.filter(value => value.tipoDocumento === "Altro").map((value, index) => <SezioneDocumento key={index}
                                                                                                           tipoDocumento={value.tipoDocumento}
                                                                                                           classeDocumentoId={value.idClasseDocumento}
                                                                                                           statoDocumento={value.statoDocumento}
                                                                                                           statoPratica={statoPratica}
                                                                                                           nota={value.nota}
                                                                                                           listaDoc={value.listaDocumenti}
                                                                                                           isProspect={isProspect}
                                                                                                           update={getDocumentazione}/>)}


      </div>}
    </>);
}
